@import "colours";
@import "fonts";
@import "media-queries";

$sidebar-width: 275px;

.img {
  max-height: 295px;
  opacity: 1;
  transition: opacity 175ms;
  display: inline-block;
  height: auto;
}

.subscribeButton {
  background-color: #b6956a;
  font: $normal-text;
  border: none;
  outline: none;
  border-radius: 26px;
  border: .1em solid white;
  color: #fff;
  display: block;
  text-decoration: none;
  text-align: center;
  padding: 0.5em 2em;
  margin: 0.3em;
  white-space: nowrap;
  -webkit-transition: background 300ms ease-in-out;
  transition: background 300ms ease-in-out;
  &:hover {
    background: $faded-brown;
  }
}

.p {
  font-size: 14px;
  letter-spacing: 0.005em;
  text-align: left;
  box-sizing: border-box;
  margin: 0.3em 0em;
}

.h3 {
  font-size: 15px;
  line-height: 28px;
  margin: 0.1em 0em;
}

@media #{$mq-xxxsmall} {
  .brand__magazineContainer, .brand__magazineContainer__sticky {
    display: none;
  }
}
@media #{$mq-xxsmall} {
  
}
@media #{$mq-small} {

}
@media #{$mq-medium} {
  .brand__magazineContainer {
    width: $sidebar-width;
    text-align: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 0em 1em;
    position: relative;
    top: 0px;
    right: 0;
    float: right;
    transition: all 0.5s; 
  }
  .brand__magazineContainer__sticky {
    width: $sidebar-width;
    text-align: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding: 0em 1em;
    position: fixed;
    top: 4.2em;
    right: 0;
    float: right;
    transition: all 0.5s;
  }
  
}
@media #{$mq-large} {
  .recpies {
    width: calc(80% - 4px);
  }

}
